import React, { useState } from 'react';

//images
import globe from '../worldwide.svg';
import UFO from '../ufo.svg';

//components
import Healthbar from './Healthbar';
import AnswerMechanic from './AnswerMechanic';
import Win from './Win';
import GameOver from './GameOver';

//key
import questions from './questionKey';

const GameScreen = ({ setting, setSetting }) => {

    const [health, setHealth] = useState(100);
    const [page, setPage] = useState(1);

    const block = {
        display: "none"
    }

    const show = {
        display: "block"
    }

    if (setting.currentPage !== 2) {
        return(null);
    }

    return (
        <div className="gamescreen">
            <Healthbar health={health} />
            <img src={globe} alt="globe" className="earth"></img>
            <img src={UFO} alt="ufo" className="ufo__1" style={page > 9 ? block:show}></img>
            <img src={UFO} alt="ufo" className="ufo__2" style={page > 9 ? block:show}></img>
            <img src={UFO} alt="ufo" className="ufo__3" style={page > 9 ? block:show}></img>
            <img src={UFO} alt="ufo" className="ufo__4" style={page > 9 ? block:show}></img>
            <img src={UFO} alt="ufo" className="ufo__5" style={page > 9 ? block:show}></img>
            {questions.map((content) => (
                <AnswerMechanic key={content.questionNum} content={content} page={page} setPage={setPage} health={health} setHealth={setHealth} />
            ))}
            <Win page={page} setPage={setPage} setSetting={setSetting} setHealth={setHealth} />
            <GameOver health={health} setSetting={setSetting} setHealth={setHealth} setPage={setPage}/>
        </div>
    )
}

export default GameScreen;