import React from 'react';

import wrong from '../sfx/wrong.mp3';
import score from '../sfx/score.mp3';

export default function AnswerCard({ data, page, setPage, health, setHealth, tries, setTries }) {

    const checker = () => {
        const errorSound = new Audio(wrong);
        errorSound.volume = 0.2;

        const scoreSound = new Audio(score);
        scoreSound.volume = 0.2;
        if (data.correct) {
            scoreSound.play();
            setTries(1);
            setPage(page + 1);
        } else {
            if (tries !== 1) {
                errorSound.play();
                setHealth(health - 25);
                setPage(page + 1);
            } else {
                errorSound.play();
                setTries(0);
            }
        }
    }

    return (
        <>
            <p onClick={checker}>{data.answer}</p>
        </>
    )
}