import React from 'react';

import go from '../sfx/select.mp3'

const MainMenu = ({ setting, setSetting }) => {
    const select = () => {
        const sound = new Audio(go);
        sound.play();
        setTimeout(() => {
            setSetting(prevSetting => ({
                ...prevSetting,
                currentPage: 2
            }))
        }, 1000);
    }

    if (setting.currentPage !== 1) {
        return (null);
    }

    return (
        <div className="mainMenu">
            <div>
                <p>The Earth is under attack by aliens and your knowledge of Earth Science is the only thing stopping them from destroying earth!</p>
                <button onClick={select}>Play</button>
            </div>
        </div>
    )
}

export default MainMenu;