import React from 'react';

const Healthbar = ({ health }) => {
    const fill = {
        width: health + '%'
    }

    return (
        <div className="health-position">
            <p>Current Health</p>
            <div className='health'>
                <div className='health__inside' style={fill}></div>
            </div>
        </div>
        
    )
}

export default Healthbar;