import React from 'react';

export default function GameOver({ health, setSetting, setPage, setHealth }) {
    const restart = () => {
        setHealth(100);
        setPage(1);
        setSetting(prevSetting => ({
            ...prevSetting,
            currentPage: 1
        }));
    }

    if (health !== 0) return (null);
    return (
        <div className="win">
            <h1>Game Over</h1>
            <button onClick={restart}>Restart</button>
        </div>
    )
}