import React, { useState } from 'react';

//components
import AnswerCard from './AnswerCard';
import Tips from './Tips';

export default function AnswerMechanic({ content, page, setPage, health, setHealth }) {

    const [tries, setTries] = useState(1);

    if (page !== content.questionNum || health === 0) return (null);

    return (
        <div className="question-placement">
            <div className="question">
                <p>{content.questionNum}.</p>
                <p>{content.question}</p>
            </div>
            <div className="answer-position">
                {content.answers.map((data) => (
                    <AnswerCard data={data} page={page} setPage={setPage} health={health} setHealth={setHealth} tries={tries} setTries={setTries} />
                ))}
            </div>
            <Tips tries={tries} content={content.tips} />
        </div>
    )
}