import React, { useState } from 'react';

//components
import MainMenu from './MainMenu';
import GameScreen from './GameScreen';

const Main = () => {
    const [setting, setSetting] = useState({
        currentPage: 1
    })

    return (
        <div className="background">
            <MainMenu setting={setting} setSetting={setSetting} />
            <GameScreen setting={setting} setSetting={setSetting} />
        </div>
    )
}

export default Main;