import React from 'react';

export default function Win({ page, setPage, setSetting, setHealth }) {
    const restart = () => {
        setHealth(100);
        setPage(1);
        setSetting(prevSetting => ({
            ...prevSetting,
            currentPage: 1
        }));
    }
    
    if (page !== 10) return(null);
    return (
        <div className="win">
            <h1>YOU SAVED EARTH!</h1>
            <button onClick={restart} >Restart</button>
        </div>
    )
}